.glitch {
  color: black;
  font-size: 30px;
  position: relative;
  width: 90px;
  margin: 0 auto;
}

@keyframes noise-anim {
  0% {
    clip: rect(8px, 9999px, 27px, 0);
  }
  5% {
    clip: rect(29px, 9999px, 73px, 0);
  }
  10% {
    clip: rect(94px, 9999px, 20px, 0);
  }
  15% {
    clip: rect(64px, 9999px, 28px, 0);
  }
  20% {
    clip: rect(81px, 9999px, 86px, 0);
  }
  25% {
    clip: rect(44px, 9999px, 65px, 0);
  }
  30% {
    clip: rect(37px, 9999px, 79px, 0);
  }
  35% {
    clip: rect(23px, 9999px, 96px, 0);
  }
  40% {
    clip: rect(80px, 9999px, 34px, 0);
  }
  45% {
    clip: rect(79px, 9999px, 92px, 0);
  }
  50% {
    clip: rect(67px, 9999px, 53px, 0);
  }
  55% {
    clip: rect(62px, 9999px, 60px, 0);
  }
  60% {
    clip: rect(44px, 9999px, 74px, 0);
  }
  65% {
    clip: rect(98px, 9999px, 61px, 0);
  }
  70% {
    clip: rect(44px, 9999px, 62px, 0);
  }
  75% {
    clip: rect(83px, 9999px, 70px, 0);
  }
  80% {
    clip: rect(98px, 9999px, 39px, 0);
  }
  85% {
    clip: rect(80px, 9999px, 82px, 0);
  }
  90% {
    clip: rect(74px, 9999px, 99px, 0);
  }
  95% {
    clip: rect(20px, 9999px, 67px, 0);
  }
  100% {
    clip: rect(67px, 9999px, 22px, 0);
  }
}
.glitch:after {
  content: attr(data-text);
  position: absolute;
  left: 2px;
  text-shadow: -1px 0 red;
  top: 0;
  color: black;
  background: white;
  overflow: hidden;
  clip: rect(0, 900px, 0, 0);
  animation: noise-anim 2s infinite linear alternate-reverse;
}

@keyframes noise-anim-2 {
  0% {
    clip: rect(7px, 9999px, 100px, 0);
  }
  5% {
    clip: rect(1px, 9999px, 77px, 0);
  }
  10% {
    clip: rect(52px, 9999px, 13px, 0);
  }
  15% {
    clip: rect(30px, 9999px, 57px, 0);
  }
  20% {
    clip: rect(38px, 9999px, 77px, 0);
  }
  25% {
    clip: rect(30px, 9999px, 73px, 0);
  }
  30% {
    clip: rect(61px, 9999px, 71px, 0);
  }
  35% {
    clip: rect(55px, 9999px, 31px, 0);
  }
  40% {
    clip: rect(6px, 9999px, 53px, 0);
  }
  45% {
    clip: rect(4px, 9999px, 90px, 0);
  }
  50% {
    clip: rect(25px, 9999px, 11px, 0);
  }
  55% {
    clip: rect(88px, 9999px, 89px, 0);
  }
  60% {
    clip: rect(45px, 9999px, 73px, 0);
  }
  65% {
    clip: rect(50px, 9999px, 12px, 0);
  }
  70% {
    clip: rect(85px, 9999px, 69px, 0);
  }
  75% {
    clip: rect(35px, 9999px, 44px, 0);
  }
  80% {
    clip: rect(19px, 9999px, 87px, 0);
  }
  85% {
    clip: rect(79px, 9999px, 22px, 0);
  }
  90% {
    clip: rect(98px, 9999px, 40px, 0);
  }
  95% {
    clip: rect(18px, 9999px, 5px, 0);
  }
  100% {
    clip: rect(99px, 9999px, 86px, 0);
  }
}
.glitch:before {
  content: attr(data-text);
  position: absolute;
  left: -2px;
  text-shadow: 1px 0 blue;
  top: 0;
  color: black;
  background: white;
  overflow: hidden;
  clip: rect(0, 900px, 0, 0);
  animation: noise-anim-2 3s infinite linear alternate-reverse;
}
