    height: 65px;
  }
  footer div.social-media-links ul {
    margin: 0;
    padding: 0;
  }
  footer div.social-media-links li {
    display: inline;
    margin: 0;
    padding: 10px;
  }
  footer div.social-media-links a {
    border-bottom: 0px solid rgba(0, 0, 0, 0.95);
